import React, { useState, useEffect } from 'react';

interface ProblemInputProps {
  onProblemChange: (problem: string) => void;
  initialValue?: string;
}

export const ProblemInput: React.FC<ProblemInputProps> = ({ onProblemChange, initialValue = '' }) => {
  const [problem, setProblem] = useState(initialValue);

  // initialValueãŒå¤‰æ›´ã•ã‚ŒãŸã‚‰å†…éƒ¨çŠ¶æ…‹ã‚’æ›´æ–°
  useEffect(() => {
    setProblem(initialValue);
  }, [initialValue]);

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const value = e.target.value;
    setProblem(value);
    onProblemChange(value);
  };

  return (
    <div style={{ 
      height: '30%', 
      display: 'flex', 
      flexDirection: 'column',
      borderBottom: '1px solid #ddd'
    }}>
      <div style={{ 
        padding: '12px 16px', 
        backgroundColor: '#f8f9fa',
        borderBottom: '1px solid #ddd',
        fontWeight: 'bold',
        fontSize: '14px'
      }}>
        å•é¡Œæ–‡
      </div>
      <textarea
        value={problem}
        onChange={handleChange}
        placeholder="ã“ã“ã«å•é¡Œæ–‡ã‚’å…¥åŠ›ã—ã¦ãã ã•ã„..."
        style={{
          flex: 1,
          padding: '16px',
          border: 'none',
          outline: 'none',
          resize: 'none',
          fontFamily: 'inherit',
          fontSize: '14px',
          lineHeight: '1.6'
        }}
      />
    </div>
  );
};